import React from 'react';
import PropTypes from 'prop-types';

const ContactIcon = (props) => (
  <a href={props.url} className="contact-link">
    <i className={'contact-icon fa fa-' + props.icon } />
  </a>
);

const Contact = (props) => {
  let contacts = props.contacts.map((c, i) => (<ContactIcon {...c} key={i} />));
  return (
    <div className="section-content">
      <div className="contact">
        <h2 className="section-content-heading">Get in touch!</h2>
        <div>
          <p>Reach out to <a href="mailto: adityasharma15696@gmail.com"><kbd>me</kbd></a> I would love to hear from you !</p>
          { contacts }
        </div>
      </div>
    </div>
)};

Contact.propTypes = {
  contacts: PropTypes.arrayOf(PropTypes.shape({
    icon: PropTypes.string.isRequired,
    url: PropTypes.string.isRequired
  })).isRequired
};

export default Contact;



