import React, { PureComponent } from 'react';
import $ from 'jquery';

class Navbar extends PureComponent {
  scrollToLink(e, changeHash=true) {
    if (e.target.hash !== '') {
      e.preventDefault();
      let hash = e.currentTarget.hash;
      console.log(hash);
      $('html, body').animate({
        scrollTop: $(hash).offset().top
      }, 800, function(){
        if(changeHash)
          window.location.hash = hash;
      });
    }
  }

  render() {
    return (
      <nav className="navbar navbar-default" id="top">
        <div className="navbar-header">
          <button type="button" className="navbar-toggle" data-toggle="collapse" data-target=".navbar-ex1-collapse">
            <span className="sr-only">Toggle navigation</span>
            <span className="icon-bar"></span>
            <span className="icon-bar"></span>
            <span className="icon-bar"></span>
          </button>
        </div>
        <div className="collapse navbar-collapse navbar-ex1-collapse">
          <ul className="nav navbar-nav">
            <li><a href="#home" onClick={this.scrollToLink}>Home</a></li>
            <li><a href="#projects" onClick={this.scrollToLink}>Projects</a></li>
            <li><a href="#about" onClick={this.scrollToLink}>About Me</a></li>
            <li><a href="#contact" onClick={this.scrollToLink}>Contact</a></li>
          </ul>
        </div>
        <a href="#top" className="scroll-top" onClick={e=>this.scrollToLink(e, false)}>
          <i className="fa fa-angle-up fa-2x"></i>
        </a>
      </nav>
    )
  }
};

export default Navbar;


