import React from 'react';
import Home from '../components/Sections/Home';
import Projects from '../components/Sections/Projects';
import Contact from '../components/Sections/Contact';
import {AboutProfessional, AboutPersonal} from '../components/Sections/About';

// icon property takes a name of fontawesome icon without fa- prefix
const contacts =  [{
  icon: 'linkedin', url: 'https://www.linkedin.com/in/aditya-dev-sharma-7b65a010b'
}, {
  icon: 'github', url: 'https://github.com/g33kyaditya'
}];


const projects = [{
  name: 'KDE Now',
  tags: ['C++', 'Qt', 'KDE Frameworks 5' ,'QML'],
  description: 'Designed and implemented as part of Google Summer of Code 2016 under KDE\nIt fits like a plasmoid on your KDE Desktop and and displays scrollable cards of important information from your email\nvhanda came up with the idea on a flight when he was looking at some Google Now cards\nI ended up creating an IMAP client for fetching emails, coupled with a plug-in-able daemon that could make sense of the data from your email\nPasswords were managed by the KDE system wallet and the data was passed to the frontent using the DBus Interface\nThe plugins are loaded at run-time so anyone can create their own new plugin on top of the IMAP client and easily add more features to it',
  images: [
    '/images/dark1.png',
    '/images/light11.png',
    '/images/light21.png',
    '/images/dark21.png'
  ],
  links: [{name: 'Google Archive', url: 'https://summerofcode.withgoogle.com/archive/2016/projects/4790279299137536/'}, {name: 'KDE', url: 'https://cgit.kde.org/kde-now.git/'}, {name: 'Demo', url: 'https://www.youtube.com/watch?v=a5bJdDuQRIQ'}]
},{
  name: 'Breakout',
  tags: ['C++', 'SFML', 'CMake'],
  description: 'I\'ve always liked arcade games and used to spend a lot of time playing them when I was younger\nI read some gamasutra, some basic game dev concepts like game loop, input handler etc and tried my hand at making this.\nWith C++ I used SFML, which is a C++ game development library\nMost of the heavy lifting is done by SFML which handles windows, graphics and inputs\nI carefully connected the dots in my game loop and managed object collisions and voila, it worked !',
  links: [{name: 'Github', url: 'https://github.com/g33kyaditya/Breakout'}],
  images: [
    '/images/breakout1.png',
    '/images/breakout2.png'
  ]
},{
  name: 'Ray Tracer',
  tags: ['C++', 'Computer Graphics', 'OOPS'],
  description: 'We had a course on Computer Graphics back in the Fall semester of 2016\nRay Tracers were not part of our coursework but their wide applications interested me\nThe project is essentially a light simulator that simulated the nature of light falling on objects. It does so by doing careful computations on bits to render an image\nWhat\'s more interesting though, is the fact that the same ray tracer could be enhanced further to render 3D real-life-like images, like from one of the Christopher Nolan\'s movies\nAll CAD and modelling softwares use a ray tracer in their engines ;)',
  links: [{name: 'Github', url: 'https://github.com/g33kyaditya/Ray-Tracer'}]
}];

const sections = [{
  id: 'home',
  bgColor: '#607d8b',
  color: '#fafafa',
  content: <Home />
},{
  id:'projects',
  bgColor: '#fff',
  color: '#333',
  headingColor: '#263238',
  title: 'Projects',
  content: <Projects projects={projects} />
}, {
  id:'about',
  bgColor: '#607d8b',
  color: '#fafafa',
  headingColor: '#263238',
  content: <AboutProfessional/>,
  title: 'About Me'
}, {
  bgColor: '#546e7a',
  color: '#fafafa',
  content: <AboutPersonal/>
}, {
  id: 'contact',
  bgColor: '#78909c',
  headingColor: '#263238',
  color: '#fafafa',
  title: 'Contact Me',
  content: <Contact contacts={contacts}/>
}];

export default sections;



