import React from 'react';
import LightboxWrapper from '../LightboxWrapper/LightboxWrapper';
import PropTypes from 'prop-types';

const Projects = props => {
  return (
    <div className="projects">
      {props.projects.map((p, i) => (
        <div key={i}>
          <div key={i} className="project-container" data-toggle="collapse" data-target={`#${i}`}>
            <div className="project-align">
              <div>
                <div className="project-name">{p.name}</div>
                <div className="tags">
                  {p.tags.map((t, j) => (<div className="tag" key={j}>{ t }</div>))}
                </div>
              </div>
            </div>
          </div>

          <div id={i} className="collapse clearfix">
            <div className="project-align project-collapse">
              <div className="project-description">{p.description}</div>
              <hr/>
              {p.images && (<LightboxWrapper images={p.images} />)}
              {p.images && (<hr />)}
              <div className="project-links">
                {p.links.map((l, j) => (
                  <div className="project-link" key={j}>
                    <strong>{ l.name }</strong> 
                    <a href={ l.url } className="gutter-left-sm">{ l.url }</a>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      ))}
    </div>
)};

Projects.propTypes = {
  projects: PropTypes.arrayOf(PropTypes.shape({
    name: PropTypes.string.isRequired,
    tags: PropTypes.arrayOf(PropTypes.string).isRequired,
    description: PropTypes.string.isRequired,
    images: PropTypes.arrayOf(PropTypes.string),
    links: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string.isRequired,
      url: PropTypes.string.isRequired
    })).isRequired
  })).isRequired
};

export default Projects;


