import React from 'react';
import PropTypes from 'prop-types';

const Section = props => {
  const containerStyles = {color: props.color, backgroundColor: props.bgColor};
  const headingStyles = { borderColor: props.headingColor, color: props.headingColor };
  const heading = (<div className="section-heading" style={headingStyles}>{ props.title }</div>);
  return (
    <section id={props.id}>
      { props.title ? heading : '' }
      <div className="container-fluid" style={containerStyles}>
        { props.content }
      </div>
    </section>
)};

Section.propTypes = {
  color: PropTypes.string.isRequired,
  bgColor: PropTypes.string.isRequired,
  content: PropTypes.any.isRequired,
  // following props are optional
  headingColor: PropTypes.string,
  title: PropTypes.any
};

export default Section;


